.footer {
  position: sticky;
  bottom: 0;
  z-index: -1;

  height: 359px;

  background-image: url(../images/footer2.png);
  background-size: cover;

  @media (max-width: 600px) {
    height: 206px;
  }

  &__logo {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 40px;
    margin: 0 auto;
  }
}
